@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
  text-align: center;
  /*padding: 2% 5%;*/
}

.App * {
  box-sizing: border-box;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

