  border-radius: 7px;
  padding: 5px 15px;
}

.hide-details-button {
  font-size: larger;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #ffccaa;
  color: black;
  border: 3px solid black !important;
  border-radius: 7px;
  padding: 5px 15px;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .numberOfEvents {
    text-align: left !important;
    padding-left: 10px;
  }
}

.numberOfEvents_input {
  width: 60px;
  margin: 0px auto;
}

